@import '../../../../App.scss';

.stepper_root {
    width: auto;
}

.stepper_container {
    width: 100%;
    display: flex;
    padding-left: 120px;
    @extend .overall_height;
}

.vertical_label {
    padding: 0 !important;
    position: relative;
}

.lineVertical {
    border-left-width: 6px !important;
    min-height: 160px !important;
}

.stepperIcon {
    position: relative;
}

.icon_root {
    width: 12px !important;
    height: 12px !important;
    color: var(--secondary-color);
    position: absolute;
    z-index: 9;
}

.completed_line {
    .lineVertical {
        border-color: var(--primary-color) !important;
    }
}

.root_connector {
    margin-left: 10px !important;
}

.label_container {
    position: absolute;
    top: -10px;
    left: 30px;
    word-break: break-word;
}

.step_root {
    width: 10px;
    height: 0;
     
    &:first-child {
        height: 2px !important;

        .vertical_label>.label_container {
            top: 0 !important;
        }

        .icon_root {
            top: -2px !important;
        }
        
        .outer_circle {
            top: -7px;
        }
    }
}

.stepper_icon {
    padding: {
        right: 0 !important;
        left: 7px
    };
}

.outer_circle {
    position: absolute;
    top: -6px;
    left: -6px;
    background-color: #1e1e1e66;
    border-radius: 20px;
    width: 24px;
    height: 24px;
}

.date_view {
    position: absolute;
    left: -150px;
    top: 7px;
    max-width: 120px;
}

@media (min-width: 901px) {
    .label_container {
        min-width: 150px !important;
    }
}


@media (max-width: 900px) {
    .label_container {
        max-width: 100px;
        width: max-content !important;
    }
}

@media (min-width: 1100px) {
    .label_container {
        min-width: 200px !important;
    }
}