.order_container {
    display: grid;
    grid-template-rows: auto;
    grid-template-columns: 320px auto auto auto auto;
    overflow: hidden;
    height: 100% !important;
}

.order_container>.order-list {
    grid-column: span 1;
}

.order_container>.orderView {
    grid-column-start: 2;
    grid-column: span 4;
    margin-left: 15px;
    overflow: hidden;
    display: flex;
    justify-content: space-between;
    flex-direction: column;
    gap: 20px;
}

.image-gallery img {
    width: 80px;
    height: 80px;
}