.product_detail_header_container {
    height: 32px;
    display: flex;
    justify-content: space-between;
}

.product_detail_secondary_header {
    font-size: 22px;
    border-bottom: 1px solid var(--quinary-color);
}

.send_mail_button {
    width: 180px;
    height: 32px;
    background: transparent;
    border-radius: 15px;
    border: 1px solid var(--primary-color);
    font-size: 18px;
    font-weight: 600;
}

.product_details_name {
    color: var(--quinary-color) !important;
    font-size: 14px;
}

.prduct_detail_value {
    font-size: 16px;
    color: var(--primary-color);
}

.prodct_detail_body {
   margin: 10px 0px 20px 0px;
}

.product_grid_container {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 10px;
}

.product_grid_item {
    padding: auto auto 10px auto;
    position: relative;
}

.note_description {
    margin: 5px auto auto auto;
}

.seller_note {
    margin: 10px auto auto auto;
    font-size: 16px;
    font-weight: 400;
    line-height: 20px;
}
