@import '../../../../App.scss';

.header {
    gap: 5px;
}

.inputBox {
    height: 20px;
    border: 1px solid var(--quinary-color);
    border-radius: 15px;
    flex-grow: 1;
    padding: 10px 20px 10px 20px;
    line-height: 20px;
    color: var(--tertiary-color);
    font-size: var(--heading3);
    overflow: hidden;
    resize: none;
    margin-bottom: 15px;
    @extend .medium-16;
}

.input-container {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: space-between;
    gap: 20px;
    flex-grow: 1;
    margin-bottom: 20px;

}

.inputBox::placeholder {
    font-size: var(--heading3);
    color: var(--quinary-color);
    line-height: 18px;
    padding-top: 2px;
    font-family: var(--app-medium-font) !important;
}

.input-label {
    margin-bottom: 10px;
}

p {
    margin: 0;
}

.button-container {
    display: flex;
    align-items: center;
    justify-content: space-between;
    gap: 20px;

    .track-button {
        margin-top: 60px;
    }
}

.track-button {
    cursor: pointer;
    font-family: var(--app-semibold-font) !important;
    display: flex;
    flex-grow: 1;
    font-size: var(--heading3);
    justify-content: space-around;
    border-radius: 15px;
    background-color: var(--primary-color);
    height: 40px;
    color: var(--secondary-color);
    justify-items: center;
    padding-top: 10px;

}