@import '../../../../App.scss';

.shipment_details_container {
    width: 100%;
    margin: 15px;
}

.shipping_grid_container {
    display: flex;
    justify-content: space-between;
}

.shipment_seperator {
    width: 1px;
    background-color: var(--quinary-color);
    height: 100%;
}

.shipment_profile_content {
    position: relative;
    width: 80px;
    height: 80px;
    border-radius: 50%;
    object-fit: cover;
    object-position: center;
}

.ship_label_c {
    font-size: 22px;
    font-family: var(--secondary-regular-font);
    color: var(--primary-color);
    font-weight: 700;
    line-height: 22px;
}

.shipment_grid_c_container {
    @extend .grid_c_container;
    gap: 20px;
}

.ship_from_content {
    @extend .grid_c_container;
    align-items: flex-start;
    gap: 10px;
}

.ship_to_content {
    @extend .grid_c_container;
    align-items: flex-end;
    gap: 10px;
}

.ship_username_c {
    font-family: var(--app-regular-font);
    font-size: 18px;
    font-weight: 400;
    line-height: 18px;
    color: var(--primary-color);
}

.ship_from_user_name {
    @extend .ship_username_c;
    text-align: left;

}

.ship_to_user_name {
    @extend .ship_username_c;
    text-align: right;
}

.address_text_c {
    font-family: var(--app-regular-font);
    font-size: 16px;
    font-weight: 400;
    line-height: 20px;
    text-align: left;
    color: var(--quinary-color);
}

.seperator_container {
    display: flex !important;
    height: 100% !important;
    justify-content: center !important;
    align-items: center !important;
}

.product_detail_header_container {
    height: 32px;
    display: flex;
    justify-content: space-between;
}

.prodct_detail_body {
    margin: 10px 0px 20px 0px;
}

.product_grid_container {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 10px;
}

.product_grid_item {
    padding: auto auto 10px auto;
    position: relative;
}

.product_details_name {
    color: var(--quinary-color) !important;
    font-size: 14px;
}

.prduct_detail_value {
    font-size: 16px;
    color: var(--primary-color);
}

.shipment_note {
    margin: 10px auto auto auto;
    font-size: 16px;
    font-weight: 400;
    line-height: 20px;
    width: 100%;
}

.note_description {
    margin: 5px auto auto auto;
}