@import '../../../../App.scss';

.shipment_header_style {
    width: 100%;
    margin: 24px 0 30px 0 !important;
}

.flex-12 {
    display: flex;
    flex-direction: column !important;
    gap: 12px;
}

.shipment_container {
    @extend .flex-12;
    @extend .overall_height;
}

.tracking_overview {
    @extend .flex-12;
    @extend .overall_height;
    position: relative;
}

.tracking_main {
    background-color: var(--secondary-color);
    flex-grow: 1 !important;
    border: {
        radius: 25px;
    };
    border: 0.5px solid var(--quinary-color);
    justify-content: center;
    overflow: {
        x: hidden;
        y: auto
    };
    padding: 20px 0 20px 20px;
    position: relative;
}

.shipment_overview_content {
    background-color: var(--secondary-color);
    flex-grow: 1 !important;
    border: {
        radius: 25px;
    };
    border: 0.5px solid var(--quinary-color);
    justify-content: center;
    overflow: auto;
}

.fade_effect {
    position: absolute;
    width: calc(100% - 30px);
    height: 80px;
    bottom: 0;
    background-color: var(--secondary-color);
    opacity: 0.3;
}

.back_navigation {
    display: flex;
    align-items: center;
    cursor: pointer;
}