@import '../../../../App.scss';

.order_content {
    border: 0.5px solid var(--quinary-color);
    border-radius: 25px;
    overflow: hidden;

    flex: {
        grow: 1
    }
}

.profile {
    height: 75px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    background-color: var(--secondary-color);
    box-shadow: 0px 4px 4px 0px rgba(30, 30, 30, 0.15);
    background-color: var(--secondary-color);
    padding: 0 15px;
    border-bottom: 0.5px solid var(--quinary-color);
    position: sticky;
}

.detail {
    display: flex;
    align-items: center;
    gap: 10px;
}

.three_dots {
    cursor: pointer;
}

.profileIcon {
    width: 42px;
    height: 42px;
    border-radius: 50%;
    box-shadow: 2px 2px 4px 0px rgba(0, 0, 0, 0.2);
    object-fit: cover;
}

.name {
    color: var(--tertiary-color);
}

.order_cell {
    background-color: var(--senary-color);
    padding: 15px;
    height: 100%;
}

.order_detail_container {
    gap: 10px;
    display: grid;
}

.order_action_grid {
    display: flex;
    justify-content: space-between;
    height: 42px;
}


.button-container .action_button {
    padding: 10px 20px;
}

.action_button {
    background-color: var(--primary-color);
    color: var(--secondary-color);
    height: 100%;
    border-radius: 15px;
    border-color: var(--primary-color);
    cursor: pointer;
    font-family: var(--app-regular-font);
}

.order_action_button {
    background-color: var(--primary-color);
}

.button-wrapper {
    flex: 1;
    margin: 0 5px;
    height: 100%;
}

.button-wrapper button {
    width: 100%;
}

.action_button:hover {
    background-color: var(--tertiary-color);
}

.order_attachment_container {
    display: grid;
    gap: 11px;
}